import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { isWebService } from "../core/ServiceNames"
import { StdoutWrite } from "../core/StdoutWrite"
import { ee } from "../core/event/EventEmitter"
import { HealthCheck } from "../core/health/HealthCheck"
import { Settings } from "../core/settings/Settings"
import { readSettings } from "../core/settings/SettingsIO"
import { Library } from "./Library"

const logger = lazy(() => mkLogger("library.RestartLibrary"))

export async function restartLibrary_(opts?: { readSettings?: boolean }) {
  if (isWebService()) {
    StdoutWrite.shutdownSync()
  }
  logger().warn("Restarting library...")
  try {
    await Library.endPriorInstance()
  } catch (error) {
    logger().warn("ending prior library threw an error:", error)
  }
  if (opts?.readSettings ?? true) {
    await readSettings.refresh()
    ee().emit("settingsChanged")
  }

  // This invalidate (most) lazy() caches (like the library UID)
  ee().emit("clearCache")

  // Most health checks and other bits are watching the libraryDir for
  // changes. Tell them to refresh:
  Settings.libraryDir.broadcastChange()

  HealthCheck.reset()

  await Library.instance()?.ready

  if (HealthCheck.runState() === "ready") {
    StdoutWrite.restartSync()
  }
  logger().warn("Library restarted!")
}
