import { mkLogger } from "../../core/Logger"
import { LastOneInWins } from "../../core/async/LastOneInWins"
import { submitTask_ } from "../../core/event/SharedState"
import { matchRotation } from "../../core/img/MatchRotation"
import { Previews } from "../../core/img/Previews"
import { writeTags } from "../../core/tags/ExifTool"
import {
  Actions,
  ensureHistoryRecords,
  mkHistoryRecords
} from "../../core/tags/History"
import { rotationToWriteTag } from "../../core/tags/Orientation"
import { isEmpty } from "../../fe/Array"
import { isTrue } from "../../fe/Boolean"
import { defer } from "../../fe/Defer"
import { stringify } from "../../fe/JSON"
import { gt0 } from "../../fe/Number"
import { thenCollect } from "../../fe/Promise"
import { isRotation } from "../../fe/Rotation"
import { Asset } from "./Asset"
import { AssetFile } from "./AssetFile"

const assetLock = defer(() => new LastOneInWins<number>("Asset"))

export async function setAssetRotation_(
  assetId: number,
  rotation: number,
  previews: Previews
) {
  if (!gt0(assetId)) {
    throw new Error("invalid asset " + stringify(assetId))
  }

  if (!isRotation(rotation)) {
    throw new Error(
      "setRotation(" + rotation + "): invalid rotation " + stringify(rotation)
    )
  }

  return assetLock().enqueue(assetId, async ac =>
    _setAssetRotation_(assetId, rotation, previews, ac.signal)
  )
}

async function _setAssetRotation_(
  assetId: number,
  rotation: number,
  previews: Previews,
  ac: AbortSignal
) {
  const start = Date.now()
  const log = mkLogger(
    "setAssetRotation(" + stringify({ assetId, rotation }) + ")"
  )

  const asset = Asset.ops().findById(assetId)
  if (asset == null) {
    return log.throw("Unknown assetId:" + assetId)
  }

  const repairAsset = defer(() => {
    void submitTask_("repairAsset", { assetId })
  })

  log.throwIfAborted(ac)

  // Determine what the correct rotation should be for each file. They were
  // looking at a preview image, so we'll use that as the exemplar.

  const exemplar = await previews.ap(assetId!).smallestFileForReducer("fit")
  if (exemplar == null) {
    repairAsset()
    return log.throw(
      "Missing previews for Asset " + assetId + ". Trying to repair.",
      {
        doNotSend: true,
        fatal: false,
        retriable: false
      }
    )
  }

  const assetFiles = asset.getAssetFiles()
  const shownAF = asset.getShown()
  if (shownAF == null) {
    repairAsset()
    return log.throw(
      "No primary asset file for Asset " + assetId + ". Trying to repair.",
      {
        doNotSend: true,
        fatal: false,
        retriable: false
      }
    )
  }

  const rotAF = async (af: AssetFile) => {
    const f = await af.posixFile()
    if (f == null || (await f.notExists())) {
      return log.tap({
        msg: "rotAF(): file is missing",
        result: undefined,
        meta: { f, af }
      })
    }
    log.throwIfAborted(ac)
    const r = await matchRotation(exemplar, f, rotation)
    const newRotation = r?.rotation ?? rotation + (af.rotation ?? 0)
    if (r == null) {
      log.warn(
        "Can't compute match rotation, deferring to rotation + af.rotation.",
        {
          assetId: assetId,
          rotation,
          file: f.nativePath,
          r,
          newRotation
        }
      )
    }
    log.throwIfAborted(ac)
    const t = rotationToWriteTag(newRotation, af.mimetype)
    if (t == null) {
      log.error("rotationToWriteTag() returned null", {
        assetId: assetId,
        rotation,
        mimetype: af.mimetype,
        file: f.nativePath
      })
      if (isTrue(af.shown)) {
        repairAsset()
      }
    } else {
      await ensureHistoryRecords(
        await f.sidecar(),
        mkHistoryRecords(Actions.set, t)
      )
      await writeTags(f, t, af.mimetype)
      return af.updateFromFile_(f)
    }
    return
  }

  const updatedAssetFiles = await thenCollect(assetFiles, rotAF)

  if (isEmpty(updatedAssetFiles)) {
    log.throw(
      "Failed to rotate asset " +
        assetId +
        ": no asset files are currently readable",
      { fatal: false, retriable: false, ignorable: true, doNotSend: true }
    )
    return
  }

  AssetFile.ops().upsert(updatedAssetFiles)

  await previews
    .apb(assetId!, assetFiles, { forceRebuildPreviews: true })
    .build_()
  log.info("setRotation(" + rotation + "): completed.", {
    elapsedMs: Date.now() - start,
    updatedAssetFiles
  })
  return updatedAssetFiles
}
