import { lazy } from "../../core/Lazy"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { normalizeRotation, Rotation } from "../../fe/Rotation"
import { time } from "../async/PromiseTimer"
import { PosixFile } from "../fs/PosixFile"
import { mkLogger } from "../Logger"
import { readRotation } from "../tags/ExifTool"
import { imageHash } from "./ImageHash"
import { compareImageHashes } from "./ImageHashComparison"

const logger = lazy(() => mkLogger("img.MatchRotation"))

/**
 * @param maybe_aRotation if set, use instead of the EXIF rotation of `a`
 * @return the rotation that should be applied to `b` to match `a`
 * post-EXIF-rotation (or post-`maybe_aRotation`)
 */
export async function matchRotation(
  a: PosixFile,
  b: PosixFile,
  maybe_aRotation?: number
): PromiseMaybe<{ rotation: Rotation }> {
  return time("img.matchRotation", async () => {
    const ih_a = await imageHash(a)
    const ih_b = await imageHash(b)

    // We want to know how much to rotate b:
    const ihc = compareImageHashes(ih_b, ih_a)
    if (ihc == null) {
      logger().warn("failed to compute image hashes", { a, b })
      return
    }

    const aRotation = (await readRotation(a)) ?? 0
    const bRotation = (await readRotation(b)) ?? 0

    // NO SRSLY https://docs.google.com/spreadsheets/d/1k8fA48w9buUAfsDEy9J-wTNR5GoTj4lZ93L3Qaw4JqM/edit?usp=sharing

    const rotation = normalizeRotation(
      bRotation - aRotation + ihc.aRotation + (maybe_aRotation ?? aRotation)
    )!

    return logger().tap({
      level: "info",
      msg: "matchRotation()",
      result: { rotation },
      meta: {
        a,
        b,
        aRotation,
        bRotation
      }
    })
  })
}
