import {
  BinaryField,
  ExifDate,
  ExifDateTime,
  ExifTime
} from "exiftool-vendored"
import { DateTime } from "luxon"
import { lazy } from "../core/Lazy"
import { defer } from "../fe/Defer"
import { errorFromJson, errorToJson } from "../fe/Error"
import { JsonReviver, addJsonRevivers } from "../fe/JSON"
import { DateInterval } from "./date/DateInterval"
import { FuzzyDate } from "./date/FuzzyDate"
import { WrappedError } from "./error/WrappedError"
import { CapturedAt } from "./tags/CapturedAt"

const ErrorReviver: JsonReviver = {
  name: "Error",
  fromJSON: errorFromJson
}

const FixErrorToJSON = defer(() => {
  ;(Error as any).prototype["toJSON"] = function toJSON() {
    return errorToJson(this)
  }
})

const FixDateTimeToJSON = defer(() => {
  ;(DateTime as any).prototype["toJSON"] = function toJSON() {
    return {
      _ctor: "DateTime",
      ...this.toObject(),
      zone: this.zoneName
    }
  }
})

const DateTimeReviver: JsonReviver = {
  name: "DateTime",
  fromJSON(ea: any) {
    // DateTime requires the zone to be thrown in as a second options hash (!!!)
    return DateTime.fromObject(ea, { zone: ea.zone })
  }
}

export const addDefaultJsonRevivers = lazy(() => {
  FixDateTimeToJSON()
  // FixBinaryFieldToJSON()
  FixErrorToJSON()
  addJsonRevivers(
    ExifDateTime,
    ExifDate,
    ExifTime,
    DateTimeReviver,
    CapturedAt,
    DateInterval,
    FuzzyDate,
    ErrorReviver,
    BinaryField,
    // no need to revive binary field
    WrappedError
  )
})
